import logo from './../logo.png';

export default function Home() {
    return (
        <div>
            <h1 style={{ fontWeight: 900 }}>Home :</h1>
            <div className="logo">
                <img src={logo} />
            </div>
            <p>Bienvenue dans votre espace de stockage Supidrive !</p>
            <p>Ici, vous pouvez stocker vos fichiers, vos documents, vos films et musiques en toute simplicit et en quelques clics. Commencer par lire les conditions dutilisations, et le mode demploi utilisateur pour vous familiariser avec les fonctionnalits. Ensuite, rendez-vous sur l'onglet My Drive, pour stocker votre premier fichier.</p>
            <p>En cas de problmes rencontrs, n'hsitez pas  contacter l'administrateur ou l'quipe Support.</p>
        </div>
    );
}